/* Check box */
.custom-checkbox > [type="checkbox"]:not(:checked) + label, .custom-checkbox > [type="checkbox"]:checked + label {
    font-weight: 300;
}

.custom-checkbox > [type="checkbox"]:not(:checked) + label::before, .custom-checkbox > [type="checkbox"]:checked + label::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    margin-top: 0;
    width: 22px;
    height: 22px;
    margin-left: 2px;
}

.custom-checkbox > [type="checkbox"]:checked + label::after {
    content: '';
    top: 6px;
    left: 6px;
    margin: 0px;
    background: #01a8fe;
    width: 10px;
    height: 10px;
    margin-left: 2px;
}

.custom-checkbox > [type="checkbox"]:checked + label::after {
    transform: scale(1);
}

/*In table*/
tr .custom-checkbox > [type="checkbox"]:not(:checked) + label::before, tr .custom-checkbox > [type="checkbox"]:checked + label::before {
    top: -17px;
}
th .custom-checkbox > [type="checkbox"]:not(:checked) + label::before, tr .custom-checkbox > [type="checkbox"]:checked + label::before {
    top: -17px;
}
tr .custom-checkbox > [type="checkbox"]:checked + label::after {
    top: -11px;
}

